import React, {useRef} from 'react'
import { useEffect, useState} from 'react';
import {useParams} from 'react-router-dom';
import Axios from 'axios';
import { useReactToPrint } from "react-to-print";
import moment from 'moment';

import Loading from '../Components/Loader'
import {apiURL} from '../APIs/config';
export default function InstallmentPreview(){


    const{id}=useParams();
    const [installment, setInstallment] = useState([]);
	const [loading, setLoading]=useState(true);
	const numbFormtter = new Intl.NumberFormat('en-IN');
    useEffect(()=>{
        Axios.get(`${apiURL}/installment-preview/${id}`)
        .then(
            (res)=>{
            setInstallment(res.data);
            setLoading(false);
            })
            
            .catch((err)=>{
               console.log(err);
            })
            console.log(installment);
    },[id]);

    const componentRef = useRef();
    const handlePrint = useReactToPrint({
      content: () => componentRef.current,
    });

    if(loading){

        return <Loading/>

    }
    else{

    return(
        <>

<div class="row" style={{marginLeft:20, marginRight:20}} ref={componentRef}>
<div style={{display:'flex', flexDirection:'row'}}>
            <div >
                <img src={`${apiURL}/image/logo.png`} style={{width:100, height:100}}/>
            </div>
            <div style={{djustifyContent:'center', textAlign:'center', marginLeft:20, marginRight:20}}>
                <div>
                    <p style={{fontSize:40, fontWeight:'bold', color:'brown'}}>NEW ABADI</p>
                </div>
                <div>
                    <p style={{fontWeight:'bold', fontSize:20}}>NAWA PIND</p>
                </div>
                <div>
                    <p>4.5 Km Sue-e-Asal Raiwind Road, Opposite Safari Garden Housing</p>
                </div>
                <div>
                    <p>Scheme, Lahore. Cell:0322-3303331. Ph: 042-35927305</p>
                </div>
            </div>    

		</div>	
		<div class="col-sm-10 col-sm-offset-1">
			<div class="widget-box">
				<div class="widget-header widget-header-large">
					<h3 class="widget-title grey lighter">
						<i class="ace-icon fa fa-leaf green"></i>
						Installment Receipt
					</h3>

					<div class="widget-toolbar no-border invoice-info">
						<span class="invoice-info-label">Name:</span>
                        <span class="red">{installment.clientName}</span>

						<br/>
						<span class="invoice-info-label">Father's/Husband Name:</span>
                        <span class="blue">{installment.fhName}</span>
                        <br/>
						<span class="invoice-info-label">CNIC:</span>
                        <span class="blue">{installment.clientCNIC}</span>
                        <br/>
						<span class="invoice-info-label">Cell:</span>
                        <span class="blue">{installment.contactNumber}</span>
                        <br/>
						<span class="invoice-info-label">Address:</span>
                        <span class="blue">{installment.residentialAddress}</span>
					</div>
                   

					<div class="widget-toolbar hidden-480">
					
							<i class="ace-icon fa fa-print"></i>
					
					</div>
				</div>

				<div class="widget-body">
					<div class="widget-main padding-24">
						<div class="row">
							<div class="col-sm-6">
								<div class="row">
									<div class="col-xs-11 label label-lg label-info arrowed-in arrowed-right">
										<b>Receipt Info</b>
									</div>
								</div>

								<div>
									<ul class="list-unstyled spaced">
										<li>
											<i class="ace-icon fa fa-caret-right blue"></i> Sr No. {installment._id}
										</li>
										<li>
											<i class="ace-icon fa fa-caret-right blue"></i>Current Date. {moment(installment.currDate).format('L')}
										</li>
										<li>
											<i class="ace-icon fa fa-caret-right blue"></i>Previous Sr No. {installment.prevSrNo}
										</li>

										<li>
											<i class="ace-icon fa fa-caret-right blue"></i>Previous Date: {moment(installment.prevDate).format('L')}
										</li>


									</ul>
								</div>
							</div>

							<div class="col-sm-6">
								<div class="row">
									<div class="col-xs-11 label label-lg label-success arrowed-in arrowed-right">
										<b>Plot Info</b>
									</div>
								</div>

								<div>
									<ul class="list-unstyled  spaced">
										<li>
											<i class="ace-icon fa fa-caret-right green"></i>Plot No: {installment.plotNo}
										</li>

										<li>
											<i class="ace-icon fa fa-caret-right green"></i>Category: {installment.plotSize}
										</li>

										<li>
											<i class="ace-icon fa fa-caret-right green"></i>Type:{installment.type}
										</li>

										<li class="divider"></li>
									</ul>
								</div>
							</div>
						</div>

						<div class="space"></div>

						<div>
							<table class="table table-striped table-bordered">
								<thead>
									<tr>
										<th class="hidden-xs">Payment Info</th>
										<th class="hidden-480">Price</th>
									</tr>
								</thead>

								<tbody>
									<tr>
										<td class="hidden-xs">
											Total Plot Price
										</td>
										<td class="hidden-480"> {numbFormtter.format(installment.totalPlotCost)} Rs.</td>
                                        
									</tr>

									<tr>
										<td class="hidden-xs">
											Previous Amount
										</td>
										<td class="hidden-480"> {numbFormtter.format(installment.prevAmount)} Rs.</td>
										
									</tr>

									<tr>

										<td class="hidden-xs"> Current Amount</td>
                                         <td class="hidden-480">{numbFormtter.format(installment.currAmount)} Rs.</td>
										
									</tr>

									<tr>
										<td class="hidden-xs"> Total Recieved Amount </td>
										<td class="hidden-480"> {numbFormtter.format(installment.totalRecAmount)} Rs.</td>
									</tr>
                                    <tr>
										<td class="hidden-xs"> Remaining Balance </td>
										<td class="hidden-480"> {numbFormtter.format(installment.remainingBalance)} Rs.</td>
									</tr>
								</tbody>
							</table>
						</div>

						<div class="hr hr8 hr-double hr-dotted"></div>

						<div class="row">
							<div class="col-sm-5 pull-right">
								<h4 class="pull-right">
									Due Date:
                                    <span class="red">{moment(installment.dueData).format('L')}</span>
								</h4>
							</div>
						</div><br/>

						<div class="space-6"></div>
						<div class="well" style={{marginTop:20}}>
                        &nbsp;&nbsp;&nbsp;&nbsp;_____________________&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; ________________________ &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;_________________________<br/>
                    Applicant Thumb Impression: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Authorized Sig: &nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Accountant Sig:
						</div><br/><br/>
                        <div class="space-6"></div>
						<div class="well">
							4.5 Km Sue-e-Asal Raiwind Road, Opposite Safari Garden Housing Scheme, Lahore. Cell: 0322-3303331, Ph: 042-35927305
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
       <button style={{width:200, height:100}} class="btn btn-success" onClick={handlePrint}>Print</button>
        </>
    )
}

}